import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { HttpClient} from '@angular/common/http';
import { Router} from '@angular/router';
@Component({
  selector: 'app-addstudent',
  templateUrl: './addstudent.component.html',
  styleUrls: ['./addstudent.component.css']
})
export class AddstudentComponent implements OnInit {

  constructor(public fb: FormBuilder, public http: HttpClient, public router: Router ) { }

  studentform = this.fb.group({
    studentname: ['', Validators.required],
    studentage: ['', Validators.required]
  } )
  url = 'https://api.mlab.com/api/1/databases/college/collections/students?apiKey=oldTpmfE2P1xyw6hqfKmFPzB0E48Vc1G';
  addsuccess() {
    console.log(this.studentform.value);
    this.http.post(this.url, this.studentform.value).subscribe((x) => {
    this.router.navigate(['/addsuccess'])
    })
  }

  ngOnInit() {
  }
  
}
