import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { StudentlsitComponent } from './studentlsit/studentlsit.component';
import { StudentdetailsComponent } from './studentdetails/studentdetails.component';
import { AddstudentComponent } from './addstudent/addstudent.component';
import { RouterModule } from '@angular/router';
import { FormsModule, ReactiveFormsModule} from '@angular/forms';
import { AddsuccessComponent } from './addsuccess/addsuccess.component';
import { HttpClientModule} from '@angular/common/http';
@NgModule({
  imports: [
    CommonModule, HttpClientModule, ReactiveFormsModule, FormsModule, RouterModule.forChild([
      {
        path: 'student',
        component: StudentlsitComponent
      },
      {
        path: 'addstudent',
        component: AddstudentComponent
      },
      {
        path: 'addsuccess',
        component: AddsuccessComponent
      }
    ])
  ],
  exports: [],
  declarations: [StudentlsitComponent, StudentdetailsComponent, AddstudentComponent, AddsuccessComponent]
})
export class StudentModule { }
